/* Giving persitent size for all tags */
* {
	box-sizing: border-box;
	transition:
		border-color ease .256s,
		background-color ease .256s;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

/* Defining the font size based on aspect ratio */
html {
	color: #222;
	font-size: 2.1vh;
	line-height: 1.4rem;
}
html.dark {
	color: #d4e7ec;
}
@media (max-aspect-ratio: 2/1) {
	html {
		font-size: 1.9vh;
	}
}
@media (max-aspect-ratio: 3/2) {
	html {
		font-size: 1.7vh;
	}
}
@media (max-aspect-ratio: 4/3) {
	html {
		font-size: 1.6vh;
	}
}
@media (max-aspect-ratio: 1/1) {
	html {
		font-size: 1.4vh;
	}
}
@media (max-aspect-ratio: 3/4) {
	html {
		font-size: 1.3vh;
	}
}
@media (max-aspect-ratio: 2/3) {
	html {
		font-size: 1.2vh;
	}
}
@media (max-aspect-ratio: 1/2) {
	html {
		font-size: 1.1vh;
	}
}


/* Costumize selection color */
::-moz-selection {
	background: #b3d4fc;
	text-shadow: none;
}
::selection {
	background: #b3d4fc;
	text-shadow: none;
}

/* Costumize placeholder */
::-webkit-input-placeholder {
	color: #015997;
}
:-moz-placeholder {
	color: #015997;
	opacity: 1;
}
::-moz-placeholder {
	color: #015997;
	opacity: 1;
}
:-ms-input-placeholder {
	color: #015997;
}
::-ms-input-placeholder {
	color: #015997;
}
::placeholder {
	color: #015997;
}

/* Basic HR styling */
hr {
	display: block;
	height: 1px;
	border: 0;
	border-top: 1px solid #ccc;
	margin: 1em 0;
	padding: 0;
}

/*
 Remove the gap between audio, canvas, iframes,
 images, videos and the bottom of their containers:
 https://github.com/h5bp/html5-boilerplate/issues/440
*/
audio,
canvas,
iframe,
img,
svg,
video {
	vertical-align: middle;
}

/* Remove default fieldset styles */
fieldset {
	border: 0;
	margin: 0;
	padding: 0;
}

/* Allow only vertical resizing of textareas. */
textarea {
	resize: vertical;
}


body {
	overflow-x: hidden;
	/*position: fixed;
	top: 0;
	left: 0;*/
}
body,
html {
	min-width: 100%;
	min-height: 100%;
	font-family: "Rubik", sans-serif;
	letter-spacing: .065rem;
	background-color: #A4CDD6;
}
.dark body,
html.dark {
	background-color: #202526;
}


/* Browser Upgrade Prompt */
.browserupgrade {
	margin: 0.2em 0;
	background: #ccc;
	color: #fff;
	padding: 0.2em 0;
}
.support {
	display: none;
	position: fixed;
	z-index: 999999;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	padding: 40px;
	background-color: inherit;
}
.cm__table {
	display: table;
	width: 100%;
	height: 100%;
}
.cm__cell {
	display: table-cell;
	vertical-align: middle;
}
@media (max-width: 300px) {
	header,
	main,
	#barba-wrapper {
		display: none!important;
	}
	body {
		width: 100vw;
		height: 100vh;
		overflow: hidden;
	}
	.support {
		display: block;
	}
}
@media (min-aspect-ratio: 3/1) {
	header,
	main,
	#barba-wrapper {
		width: 100vw;
		height: 100vh;
		overflow: hidden;
		display: none!important;
	}
	body {
		width: 100vw;
		height: 100vh;
		overflow: hidden;
	}
	.support {
		display: block;
	}
	.support h1 {
		font-size: 30vh;
		float: left;
		text-align: right;
		width: 25%;
	}
	.support p,
	.support h3,
	.support h4 {
		padding: 0;
		float: right;
		width: 70%;
	}
	.support h3 {
		margin: 0 0 3vh;
		font-size: 10vh;
	}
	.support h4 {
		margin: 0 0 2vh;
		font-size: 8vh;
	}
	.support p {
		margin: .25vh 0;
		font-size: 5vh;
	}
}

.login {
	width: 100vw;
	height: 100vh;
	background-image: url(../img/background-small.jpg);
}
@media (min-width: 600px) {
	.login {
		background-image: url(../img/background.jpg);
	}
}

.transition,
.transition:before,
.transition:after,
.transition a,
.transition p,
.transition i,
.transition ul,
.transition li,
.transition img,
.transition span,
.transition span:before,
.transition span:after,
.transition div {
	transition:
		color ease .256s,
		opacity ease .128s,
		background-color ease .128s,
		color ease .128s,
		left ease .128s,
		right ease .128s,
		top ease .128s,
		bottom ease .128s,
		transform ease .128s;
}
.pswp--open .pswp__container {
	transition:
		transform ease .128s;
}
.notransition a,
.notransition a:before,
.notransition a:after,
.notransition i,
.notransition p,
.notransition li,
.notransition div,
.notransition span,
.notransition input,
.notransition button {
	transition: initial;
}

