.table {
	display: table;
}

.block {
	display: block;
}
.block__indent {
	text-indent: -666em;
}
.block__left {
	float: left;
}
.block__right {
	float: right;
}
.block__clear {
	display: block;
	clear: both;
}
.block__clear:before,
.block__clear:after {
	content: "";
	display: block;
	width: 100%;
	height: 0;
	clear: both;
}
.block__center {
	position: absolute!important;
	left: 50%;
	-webkit-transform: translate3d(-50%,0,0);
	-moz-transform: translate3d(-50%,0,0);
	transform: translate3d(-50%,0,0);
}
.block__full {
	display: table;
	width: 100vw;
	height: 100vh;
}
.block__full .block__middle {
	display: table-cell;
	vertical-align: middle;
}
