.flat {
	display: table;
	clear: both;
}
.plain,
.plain li,
.flat,
.flat li {
	list-style: none;
	padding: 0;
	margin: 0;
}
.flat li {
	float: left;
}

.icn {
	position: relative;
	display: inline-block;
	width: 24px;
	height: 24px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
}
.icn:before,
.icn:after {
	content: "";
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate3d(-50%,-50%,0);
}
/* Dwan Icon Scroll */
.icn_scroll:before {
	width: 50%;
	height: 100%;
	border: 2px solid #C3DEE4;
	border-radius: 100px;
}
.icn_scroll:after {
	top: 30%;
	width: 10%;
	height: 30%;
	background-color: #C3DEE4;
	border-radius: 666px;
}
@media (max-aspect-ratio: 1/1) {
	.icn_scroll:before {
		border-width: 1px;
	}
}
/* Draw Icon Close */
.icn_close:before,
.icn_close:after {
	width: 100%;
	height: 15%;
	background-color: #000;
	border-radius: 100px;
	transform: translate3d(-50%,-50%,0) rotate(45deg);
}
.icn_close:after {
	transform: translate3d(-50%,-50%,0) rotate(-45deg);
}


form {
	display: block;
}
form input[type="text"],
form input[type="password"] {
	border: 0;
	background: transparent;
	display: block;
	width: 100%;
	padding: 0;
	margin: 0;
	line-height: 100%;
	height: auto;
	letter-spacing: .02rem;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}
form button,
form input[type="reset"],
form input[type="submit"] {
	border: 0;
	background: #015997;
	color: #fff;
	font-weight: 700;
	display: block;
	padding: 13px 20px;
	margin: 0;
	line-height: 100%;
	height: auto;
	letter-spacing: .02rem;
	cursor: pointer;
	appearance: none;
	border-radius: 0;
}
form button:hover,
form input[type="reset"]:hover,
form input[type="submit"]:hover {
	transform: translate3d(0,-2px,0);
}


/* Header */

header {
	position: fixed;
	z-index: 2;
	left: 0;
	right: 0;
	pointer-events: none;
}


/* Header logo */
.logo {
	position: absolute;
	top: 50%;
	left: 2.34vw;
	right: 0;
	width: 3.44vw;
	max-width: 100%;
	pointer-events: auto;
	transform: translate3d(0,-50%,0);
}
.logo svg {
	position: absolute;
	left: 0;
	right: 0;
	top: 50%;
	bottom: 0;
	display: block;
	transform: translate3d(0,-50%,0);
	pointer-events: none;
}
.logo svg path {
	fill: #000;
}
.dark .logo svg path,
.dark .logo svg rect {
	fill: #A4CDD6;
}
@media (max-aspect-ratio: 3/2) {
	.logo,
	.no-js .logo:before,
	.no-js .logo:after {
		width: 4.44vw;
	}
}
@media (max-aspect-ratio: 1/1) {
	.logo {
		width: 5.44vw;
		left: 3.34vw;
	}
	.no-js .logo:before,
	.no-js .logo:after {
		width: 5.44vw;
		height: 6vh;
	}
	.logo:after {
		top: 2vh;
		left: 2vw;
		filter: blur(1vw);
	}
}
@media (max-aspect-ratio: 2/3) {
	.logo,
	.no-js .logo:before,
	.no-js .logo:after {
		width: 7.44vw;
	}
	.no-js .logo:before,
	.no-js .logo:after {
		height: 8vh;
	}
	.no-js .logo:after {
		top: 1vh;
		left: 1.5vw;
	}
}


/* Main menu Icon */
.menu {
	position: absolute;
	top: 0;
	bottom: 0;
	right: 2.334vw;
	width: 2.23vw;
	pointer-events: auto;
}
.menu > a {
	position: relative;
	top: 50%;
	display: block;
	width: 100%;
	height: 100%;
	transform: translate3d(0,-50%,0);
}
.menu > a svg {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 100%;
	height: 100%;
	transform: translate3d(-50%,-50%,0);
	pointer-events: none;
}
.dark .menu > a line {
	stroke: #C3DEE4;
}
@media (max-aspect-ratio: 3/2) {
	.menu {
		width: 2.73vw;
	}
}
@media (max-aspect-ratio: 1/1) {
	.menu {
		right: 3.334vw;
		width: 3.23vw;
	}
}
@media (max-aspect-ratio: 2/3) {
	.menu {
		width: 5.23vw;
	}
}


/* Chat buble */
.chat__bubble,
.chat__bubble:after {
	padding: 1.25vw 2vw;
	border-radius: 1vw;
}
.chat__bubble {
	position: absolute;
	right: 0;
	background-color: #000;
	color: #fff;
	margin-top: 1vh;
	letter-spacing: 110%;
	line-height: 140%;
}
.chat__bubble:before {
	content: "";
	display: block;
	position: absolute;
	top: -.9vw;
	right: 1.5vw;
	display: block;
	border-bottom: 1vw solid #000;
	border-left: 1vw solid transparent;
	border-right: 1vw solid transparent;
}
.chat__bubble:after {
	content: "";
	position: absolute;
	display: block;
	top: 10%;
	left: 10%;
	right: 0;
	bottom: 0;
	background-color: rgba(0,0,0,.25);
}
@media (max-aspect-ratio: 1/1) {
	.chat__bubble,
	.chat__bubble:after {
		padding: 2vw 4vw;
		border-radius: 1.5vw;
	}
	.chat__bubble:before {
		top: -1.1vw;
		right: 1.75vw;
		border-bottom-width: 1.2vw;
		border-left-width: 1vw;
		border-right-width: 1vw;
	}
}
@media (max-aspect-ratio: 2/3) {
	.chat__bubble,
	.chat__bubble:after {
		padding: 2.5vw 5vw;
		border-radius: 2vw;
	}
	.chat__bubble:before {
		top: -1.4vw;
		right: 3vw;
		border-bottom-width: 1.5vw;
		border-left-width: 1.3vw;
		border-right-width: 1.3vw;
	}
}


